@keyframes fade-left-to-right {
  from {
    opacity: 0;
    transform: translateX(-250px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-right-to-left {
  from {
    opacity: 0;
    transform: translateX(250px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes swing {
  0% {
    transform: rotate(6deg);
  }

  4% {
    transform: rotate(-6deg);
  }

  8% {
    transform: rotate(3deg);
  }

  12% {
    transform: rotate(-3deg);
  }

  16% {
    transform: rotate(6deg);
  }

  20% {
    transform: rotate(-6deg);
  }

  24% {
    transform: rotate(0);
  }
}

