.button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;

    color: $bg-color;
    font-weight: 600;
    font-size: 20px;
    line-height: 1.3;

    border-radius: 45px;
    border: none;
    margin: 0 auto;
    cursor: pointer;

    // opacity: 0;
    background-color: $bg-color-2;

    &:hover,
    &:focus,
    &:active {
        color: $bg-color-2;
        background-color: $bg-color;
    }
}

