* {
    font-family: $font-mono;
}

body {
    background-color: $bg-color;
    height: 100%;
}

ul,
ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    color: $bg-color;
}

h1,
h2,
h3,
p {
    margin: 0;
    padding: 0;
}

